<template>
  <div id="vedioBackplay">
    <head-top title="录播回放"></head-top>

    <t-video ref="video" :src="videoItem.src" :poster="defaultPost"></t-video>
    <div class="video_info">
      <div class="info_title">{{ videoItem.title }}</div>
      <div class="info_mark">
        <van-tag mark type="success">{{ videoItem.typeName }}</van-tag>
        <div>{{ videoItem.time | formatDate(videoItem.time) }}</div>
      </div>
    </div>

    <div v-if="restVideoList" class="rest_list">
      <div v-for="(item, index) in restVideoList" :key="index" class="line_mark"
        @click="select(item)">
        <img :src="defaultPost" alt="" />
        <div class="line_right">
          <div class="line_info">{{ item.title }}</div>
          <van-tag mark type="success">{{ item.typeName }}</van-tag>
          <div>{{ item.time | formatDate(item.time) }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tVideo from "@/components/r-video";
import defaultPost from "@/assets/video/videopost.png";

export default {
  name: "",
  components: { tVideo },
  props: {},
  data() {
    return {
      defaultPost,
      selectIndex: parseInt(this.$route.params.index || "0"),
      dataList: [],
      playerOptions: {
        autoplay: false,
        muted: true,
        language: "en",
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [
          {
            type: "video/mp4",
            // mp4
            src: "http://vjs.zencdn.net/v/oceans.mp4"
            // webm
            // src: "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm"
          }
        ],
        poster:
          "https://surmon-china.github.io/vue-quill-editor/static/images/surmon-1.jpg"
      }
    };
  },
  computed: {
    videoList() {
      if (this.$route.params.type === "0") {
        return this.$store.getters.liveLession.teachers;
      } else {
        return this.$store.getters.liveLession.students;
      }
    },
    videoItem() {
      return this.videoList[this.selectIndex];
    },
    restVideoList() {
      return this.videoList
        .map((v, i) => {
          return { ...v, index: i };
        })
        .filter(r => {
          return r.index !== this.selectIndex;
        });
    }
  },
  methods: {
    // change(e, index) {
    //   if (this.selectIndex !== -1) {
    //     if (this.selectIndex !== index) {
    //       // if (this.$refs["video" + this.selectIndex][0].state.playing)
    //       this.$refs["video" + this.selectIndex][0].stopPlay();
    //       this.selectIndex = index;
    //     }
    //   } else {
    //     this.selectIndex = index;
    //   }
    // },
    select(item) {
      this.selectIndex = item.index;
      this.$refs["video"].stopPlay();
      this.$refs["video"].init();
      setTimeout(() => {
        this.$refs["video"].clickPlayBtn();
        window.scrollTo(0, 0);
      }, 200);
    }
  }
};
</script>

<style lang="less">
#vedioBackplay {
  width: 100%;
  height: 100%;
  background-color: #fff;
  .video_info {
    text-align: left;
    font-size: 1.25rem;
    line-height: 1.5;
    padding: 2vw 3vw;

    .info_title {
      word-break: break-all;
      word-wrap: break-word;
    }

    .info_mark {
      margin-top: 1vw;
      display: flex;
      justify-content: space-between;
    }
  }

  .video-player {
    width: 100vw;
    min-height: 55vw;
    margin: 0 auto;
    position: relative;
    overflow: hidden;

    video {
      width: 100%;
      margin: 0 auto;
    }
  }

  .rest_list {
    padding-top: 2vw;
    border-top: 0.01rem solid #f1f1f1;
    .line_mark {
      position: relative;
      display: flex;
      justify-content: start;
      margin: 0 3vw;
      font-size: 1.25rem;
      line-height: 1.5;
      color: #777;
      padding: 2vw 0;
      &:after {
        content: "";
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 1px;
        border-top: 1px solid #f0f1f1;
        transform: scaleY(0.6);
      }

      img {
        max-width: 26vw;
        max-height: 26vw;
        padding-right: 4vw;
      }
      .line_right {
        text-align: left;
        word-break: break-all;
        .line_info {
          height: 8vw;
        }
      }
    }
  }
}
</style>



